
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600|Scope+One");
html,
body {
  background-color: #FFF;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #001e41;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
}
main {
  text-align: center;
  margin: 25px;
}
.button {
  background-color: transparent;
  border: 2px solid #00aae0;
  display: inline-block;
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1.7;
  padding: 8px;
  text-align: center;
  text-decoration: none;
  -webkit-transition: all 125ms ease;
  transition: all 125ms ease;
  min-width: 180px;
}
.button-back {
    color: #00aae0;
}
.button-back:hover {
      background-color: #00aae0;
      color: #FFF;
}
.button-next {
    background-color: #00aae0;
    color: #FFF;
}
.button-next:hover {
      background-color: transparent;
      color: #00aae0;
}
h1 {
  color: #003478;
  font-family: "Scope One", serif;
  font-size: 1.5rem;
  font-weight: 400;
  margin: 0;
  padding: 0;
}
