      transition: all 0.2s ease;
}
.super-card .invest-slider .slider-button svg {
        height: 16px;
        width: 16px;
}
.super-card .invest-slider .slider-button svg path {
          fill: #FFF;
}
.super-card .invest-slider .slider-button:hover, .super-card .invest-slider .slider-button:focus {
        background-color: #00aae0;
        outline: 0;
}
.super-card .invest-slider .slider-holder {
      -webkit-box-flex: 10;
          -ms-flex: 10;
              flex: 10;
}
.super-card .invest-slider .slider-holder .vue-slider-component .vue-slider-tooltip {
