}
.super-card .invest-data .summary {
      min-height: 200px;
      text-align: left;
}
.super-card nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
