}
@media only screen and (max-width: 768px) {
main {
    margin: 3px;
}
h1 {
    font-size: 1.1rem;
}
h2 {
    font-size: 2rem;
    line-height: 1.1;
}
.super-card {
    padding: 8px 10px 9px;
}
.super-card .invest-slider {
