        -webkit-box-ordinal-group: 3;
            -ms-flex-order: 2;
                order: 2;
}
.super-card .invest-data .summary {
        -webkit-box-ordinal-group: 2;
            -ms-flex-order: 1;
                order: 1;
}
.super-card nav .button {
      margin-top: 10px;
      width: 100%;
}
}
.sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}



